/* ----- typewriter effect ------- */

.Home_typewriter__25koF > h1 {
  width: 100%;
  font-size: 1.5rem;
  font-weight: 300;
}

.Home_typewriter__25koF h1 {
  overflow: hidden; /* Ensures the content is not revealed until the animation */
  border-right: 0.15em solid #f9826c; /* The typwriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  margin: 0; /* Gives that scrolling effect as the typing happens */
  letter-spacing: 0.15em; /* Adjust as needed */
  -webkit-animation: Home_typing__23XJY 3.5s steps(3  0, end), Home_blink-caret__7rrQS 0.75s ease-out infinite;
          animation: Home_typing__23XJY 3.5s steps(3  0, end), Home_blink-caret__7rrQS 0.75s ease-out infinite;
}

/* The typing effect */
@-webkit-keyframes Home_typing__23XJY {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
@keyframes Home_typing__23XJY {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

/* The typewriter cursor effect */
@-webkit-keyframes Home_blink-caret__7rrQS {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #f9826c;
  }
}
@keyframes Home_blink-caret__7rrQS {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #f9826c;
  }
}

